import './App.css';
import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


function Box(warna,text,tinggi,link) {
  let divStyle = {
    backgroundColor: warna,
    width: "100vw",
    height: tinggi,
    padding: "1em 3em",
    boxSizing: "border-box",
    textAlign: "center",
    color: "white",
  }
  return (
    <div style={divStyle}>
      <Link to={link} style={{
        color: "white",
        textDecoration: "none"
      }}>
      <h2>{text}</h2>
      </Link>
    </div>
  )
}

function BoxWithImage(warna,text,tinggi,link,image) {
  let divStyle = {
    backgroundColor: warna,
    width: "100vw",
    height: tinggi,
    padding: "1em 3em",
    boxSizing: "border-box",
    textAlign: "center",
    color: "white",
  }
  return (
    <div style={divStyle}>
      <Link to={link} style={{
        color: "white",
        textDecoration: "none"
      }}>
      <h2 style={{
        display: "flex",
        justifyContent: "space-evenly"
      }}><img src={image} width="90px" height="90px"/>{text}</h2>
      </Link>
    </div>
  )
}

